      <motion.path d="M7.5 6.5l-.9-3.6a1 1 0 0 1 1-1.2h8.8a1 1 0 0 1 1 1.2l-.9 3.6" {...(isDraw ? pathAnimationProps : {})} pathLength={1} className={isDraw ? 'draw-path' : ''}/>
